.ym-form {
  margin-bottom: 20px;
}
.ym-form__text {
  margin-top: 0;
  font-size: 18px;
}
.ym-form__row {
  &--info {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
    row-gap: 14px;
    margin-bottom: 20px;
    @include vp-767 {
      grid-template-columns: 1fr;
    }
  }
}

.ym-form__input {
  border: 1px solid #5b5b5b;
  padding: 5px;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  color: #5b5b5b;
}
.ym-form__price-input {
  background-color: $color-transparent;
  max-width: 76px;
  width: auto;
  border: none;
  padding: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 18px;
  color: #5b5b5b;
}
.ym-form__button {
  padding: 8px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
  color: $text-action;
  background-color: $color-default-white;
  border: 1px solid $color-default-green;
  text-transform: uppercase;
  transition: border-color $trans-default, background-color $trans-default,
    color $trans-default;
  color: $text-light;
  background-color: $color-default-green;
  border: 1px solid $color-default-dark;
  cursor: pointer;

  @include vp-767 {
    width: 60%;
  }

  @include hover-focus {
    outline: none;
    color: $text-light;
    background-color: $color-default-green;
    border: 1px solid $color-default-dark;

    @include active {
      opacity: 0.7;
    }
  }

  &--disabled {
    color: $text-dark;
    pointer-events: none;
    background: rgba(2, 102, 193, 0.1);
  }

  &.is-active {
    color: $text-light;
    background-color: $color-default-green;
    border: 1px solid $color-default-dark;
  }
}
.ym-form__row {
  &--item {
  }
  &--customer-info {
  }
  &--pay {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    & span:first-child {
      margin-right: 10px;
    }
  }
}
.ym-form__subtitle {
  margin: 0;
  margin-bottom: 20px;
}
